.productCard{
        width: 30%;
        height: 380px;
        box-shadow: 0px 2px 2px rgba($color: #000000, $alpha: 0.4);
        margin-bottom: 30px;
        .productCard__info{
            @include flex(row, nowrap, center, center);
            height: 55%;
            width: 100%;
            .productCardinfo__img{
                height: 100%;
                width: 50%;
                @include flex(column, nowrap, center, center);
                img{
                    width: 100%;
                }
            }
            .productCardinfo__texto{
                width: 50%;
                height: 100%;
                @include flex(column, nowrap, space-evenly, flex-start);
                padding: 15px;
                h3{
                    font-size: 24px;
                    font-weight: bold;
                }
                span{
                    font-size: 24px;
                    font-weight: bold;
                }
                .productCardinfo__texto--descripcion{
                    color: #606061;
                }
                .productCardinfo__texto--preciofinal{
                    color: $titulos;
                    font-size: 18px;
                    font-weight: bold;
                }
                .productCardinfo__texto--precioTachado{
                    font-size: 16px;
                    color: #363637;
                    text-decoration: line-through;
                }
            }
        }
        .productCard__otros{
            @include flex(column, nowrap, space-evenly, flex-start);
            height: 10%;
            p{
                color: #606061;
            }
            a{
                &:hover{
                    color: $titulos;
                    cursor: pointer;
                }
            }
        }
    }