.categoryCard{
    box-shadow: 0px 2px 2px rgba($color: #000000, $alpha: 0.5);
    width: 45%;
    height: 200px;
    padding: 20px 0px;
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 12px;
    @include flex (row, wrap, center, center);
    @include BgImage(cover, center, no-repeat);
    cursor: pointer;
    h2{
        color: white;
        font-size: 40px;
        background: linear-gradient((rgba(0,0,0,1), rgba(0,0,0,0.4)));
    }
}
.categoryCard__Center{
    width: 100%;
    box-shadow: 0px 1px 1px rgba($color: #000000, $alpha: 0.5);
    height: 250px;
    border-radius: 12px;
    @include flex (column, wrap, center, center);
    @include BgImage(cover, center, no-repeat);
    cursor: pointer;
    h2{
        color: white;
        font-size: 40px;
        background: linear-gradient((rgba(0,0,0,1), rgba(0,0,0,0.4)));
    }
}
